
import React from 'react'
import config from '../../config'
import ChainId from '../../constants/ChainId'
import SocialIcon from './SocialIcon'

import './Socials.css'

const contractAddress =
  config.depositContract[config.chainId.mainnet] ||
  config.depositContract[config.chainId.test]

const socials = [
  {
    type: config.scan.type,
    title: 'Contract',
    href: `https://${config.scan.domain}/${config.scan.addressPath}/${contractAddress}`,
  },
  {
    type: 'telegram',
    title: 'Telegram',
    href: config.telegram
  },
  {
    type: 'twitter',
    title: 'Twitter',
    href: config.twitter
  },
  {
    type: 'docs',
    title: 'WhitePaper',
    href: 'https://docs.hiveminer.finance'
  }
]

const Social: React.FC<any> = ({ type, title, href }) => (
  <div className="social">
    <a href={href} title={title} target="_blank" rel="noopener">
      <SocialIcon type={type} />
    </a>
  </div>
)

const Socials = () => (
  <div className="socials">
    {socials.map((item, index) => (
      <Social key={index} {...item} />
    ))}
  </div>
)

export default Socials