import { InjectedConnector } from "@web3-react/injected-connector";
import { WalletConnectConnector } from "@web3-react/walletconnect-connector";

import ChainId from "../../constants/ChainId";
import { NetworkConnector } from "./NetworkConnector";
import config from '../../config'
export enum WALLET_CONNECTOR {
  METAMASK = "MetaMask",
  WALLET_CONNECT = "WalletConnector",
  TRUST_WALLET = "TrustWallet",
}

export const walletLocalStorageKey = "wallet";
export const walletConnectLocalStorageKey = "walletconnect";

export const supportChainIdList: ChainId[] = [config.chainId.mainnet, config.chainId.test];

export const RPC: any = {
  [config.chainId.mainnet]: config.rpc.mainnet,
  [config.chainId.test]: config.rpc.test,
};

export const network = new NetworkConnector({
  defaultChainId: config.chainId.mainnet,
  urls: RPC,
});

export const createNetwork = (chainId: ChainId) =>
  new NetworkConnector({
    defaultChainId: chainId,
    urls: RPC,
  });

export const injected = new InjectedConnector({
  supportedChainIds: supportChainIdList,
});

const walletconnectConfig = {
  rpc: {
    [config.chainId.mainnet]: config.rpc.mainnet,
    [config.chainId.test]: config.rpc.test,
  },
  bridge: "https://bridge.walletconnect.org/",
  qrcode: true,
  supportedChainIds: supportChainIdList,
};

export const walletConnectWithDefaultChainId = (chainId = config.chainId.mainnet) => {
  const sortedSupportedChainIds = supportChainIdList.sort((a) => {
    if (a === chainId) {
      return -1;
    }
    return 1;
  });
  const wc = {
    ...walletconnectConfig,
    supportedChainIds: sortedSupportedChainIds,
  };

  return new WalletConnectConnector(wc);
};

export const connectorList = {
  [WALLET_CONNECTOR.METAMASK]: injected,
  [WALLET_CONNECTOR.WALLET_CONNECT]: walletConnectWithDefaultChainId(),
  [WALLET_CONNECTOR.TRUST_WALLET]: injected,
};

export default connectorList;