import React from 'react';
import cn from 'classnames';

import './Section.css';

interface SectionProps {
    className?: string;
    children?: any;
    small?: boolean;
}

const Section: React.FC<SectionProps> = ({className, children, small}) => {
    return (
        <div className={cn("section", className, {"section--small": small})}>
            {children}
        </div>
    )
}

export default Section;