import React from 'react'

import Card from '../../components/Card/Card'
import InfoItem from '../../components/InfoItem/InfoItem'
import Section from '../../components/Section/Section'
//import HelpfulLinks from '../../components/HelpfulLinks'
import { useBnbHive } from '../../providers/BNBHiveProvider/BNBHiveContext'
import { fromWeiWithDecimals, roundTo } from '../../utils'

import './InfoSection.css'
import Loader from '../../components/Loader/Loader'
import config from '../../config'
const {symbol} = config

const InfoSectionItem: React.FC<any> = ({ label, children, loading }) => {
  return (
    <div className="info-section__item">
      <div className="info-section__item-value">
        {loading ? <Loader /> : children}
      </div>
      <div className="info-section__item-label">{label}</div>
    </div>
  )
}

const InfoSection: React.FC = () => {
  const { stats } = useBnbHive()
  const tvl = roundTo(fromWeiWithDecimals(stats.tvl || '0'), 2)
  const vaultBalance = roundTo(fromWeiWithDecimals(stats.vault || '0'), 2)
  return (
    <Section className="info-section" small>
      <Card title="Stats">
        <div className="info-section__items">
          <InfoSectionItem label="TVL" loading={stats.loading}>
            {tvl} {symbol}
          </InfoSectionItem>
          <InfoSectionItem label="APR" loading={stats.loading}>
            {stats.dailyReturnPercent * 365}%
          </InfoSectionItem>
          <InfoSectionItem label="Dev Fee" loading={stats.loading}>
            {stats.devFee}%
          </InfoSectionItem>
          <InfoSectionItem label="Vault Fee" loading={stats.loading}>
            {stats.vaultFee}%
          </InfoSectionItem>
          <InfoSectionItem label="Vault" loading={stats.loading}>
            {vaultBalance} {symbol}
          </InfoSectionItem>
        </div>
      </Card>
    </Section>
  )
}

export default InfoSection