import React, { useState } from 'react'

import Button from '../../components/Button/Button'
import Card from '../../components/Card/Card'
import ConnectWallet from '../../components/ConnectWallet/ConnectWallet'
import InfoItem from '../../components/InfoItem/InfoItem'
import Input from '../../components/Input/Input'
import Section from '../../components/Section/Section'
import useActiveWeb3React from '../../hooks/useActiveWeb3React'
import { useBnbHive } from '../../providers/BNBHiveProvider/BNBHiveContext'
import { fromWeiWithDecimals, roundTo } from '../../utils'

import './HireSection.css'

import config from '../../config'
const {symbol} = config

interface ErrorMessageProps {
  isInitialised: boolean
  parsedAmount: number;
  minDeposit: number;
  maxDeposit: number;
  balance: number;
}

const ErrorMessage: React.FC<ErrorMessageProps> = ({isInitialised, parsedAmount, minDeposit, maxDeposit, balance }) => {
  const isLowerMinimumAmount = parsedAmount < minDeposit;
  const isHigherMaximumAmount = parsedAmount > maxDeposit;
  const isEnoughBalance = parsedAmount < balance;
  let text = "";
  if (!isInitialised) {
    text = "Mining is not initialized yet.";
  } else if(isLowerMinimumAmount) {
    text = `Minimum deposit amount is ${minDeposit} ${symbol}`;
  } else if(isHigherMaximumAmount) {
    text = `Your maximum deposit amount is ${roundTo(maxDeposit, 2)} ${symbol} for now.`;
  } else if(!isEnoughBalance) {
    text = `Not enough ${symbol}`;
  }
  if (!text) return null;
  return (
    <div className="hire-form__error-message">
      {text}
    </div>
  )
}

const HireForm = () => {
  const { account, balance } = useActiveWeb3React()
  const bnbHive = useBnbHive()
  const dailyIncomePercent = bnbHive.stats.dailyReturnPercent
  const [amount, setAmount] = useState<string>('1')
  const [loading, setLoading] = useState<boolean>(false)
  const handleChange = (e: React.SyntheticEvent<HTMLInputElement>) => {
    const { value } = e.target as HTMLInputElement

    const formattedValue = value
      .replace(/[^0-9.]/g, '')
      .replace(/(\..*)\./g, '$1')

    setAmount(formattedValue)
  }

  const parsedAmount = parseFloat(amount)
  const dailyIncomeAmount = roundTo(
    (parsedAmount * dailyIncomePercent) / 100,
    2,
  )

  const onHire = async () => {
    try {
      setLoading(true)
      if (bnbHive.contract && account) {
        await bnbHive.contract.deposit(amount, bnbHive.referrerAddress, account)
        setLoading(false)
      } else {
        throw new Error('No account')
      }
    } catch (e) {
      setLoading(false)
      alert((e as any).message)
    }
  }

  const isInitialised = bnbHive.stats.initialized

  const { maxDeposit, minDeposit } = bnbHive.account;

  const parsedBalance = parseFloat(fromWeiWithDecimals(balance));

  const canDeposit = (parsedAmount <= maxDeposit) && (parsedAmount >= minDeposit) && isInitialised && (parsedAmount <= parsedBalance);

  return (
    <div className="hire-form">
      <div className="hire-form__label">
        Enter amount of {symbol} you want to spend to hire bees:
      </div>
      <div className="hire-form__inputs">
        <Input value={amount} onChange={handleChange} affix={symbol} />
        {account ? (
          <Button
            type="primary"
            onClick={onHire}
            loading={loading}
            disabled={!canDeposit}
          >
            Hire Bees
          </Button>
        ) : (
          <ConnectWallet />
        )}
      </div>
      <ErrorMessage
        isInitialised={isInitialised}
        parsedAmount={parsedAmount}
        minDeposit={minDeposit}
        maxDeposit={maxDeposit}
        balance={parsedBalance}
      />
      <div className="hire-form__predict-calcs">
        ~ {dailyIncomeAmount} {symbol} daily / ~ {roundTo(dailyIncomeAmount * 7, 3)}{' '}
        {symbol} weekly
      </div>
    </div>
  )
}

const coinPerWeek = `${symbol} Per Week`

const HireSection = () => {
  const { balance } = useActiveWeb3React()
  const { account, stats } = useBnbHive()
  const dailyIncome = account.dailyIncome
  return (
    <Section className="hire-section">
      <Card>
        <InfoItem label="Your Balance">
          {roundTo(fromWeiWithDecimals(balance), 2)} {symbol}
        </InfoItem>
        <InfoItem label="Your Bees" loading={account.loading}>
          {roundTo(account.bees)}
        </InfoItem>
        <InfoItem label={coinPerWeek} loading={account.loading}>
          ~ {roundTo(dailyIncome * 7, 2)} {symbol}
        </InfoItem>
        <InfoItem label="Daily Return" loading={stats.loading}>
          {stats.dailyReturnPercent}%
        </InfoItem>
        <HireForm />
        <InfoItem label="Total deposited" loading={account.loading}>
          {account.totalDeposit} {symbol} / {roundTo(account.totalDeposit + account.maxDeposit)} {symbol}
        </InfoItem>
        <InfoItem label="Minimum deposit" loading={account.loading}>
          {account.minDeposit} {symbol}
        </InfoItem>
      </Card>
    </Section>
  )
}

export default HireSection