
import React, { useState, useMemo, useEffect } from 'react'
import config from '../../config'
import ChainId from '../../constants/ChainId'

import BnbHiveContract from '../../contracts/BnbHive'
import Web3Provider from '../../contracts/utils/Web3Provider'

import useActiveWeb3React from '../../hooks/useActiveWeb3React'
import useReferrer from '../../hooks/useReferrer'
import useRefresh from '../../hooks/useRefresh'
import { RPC } from '../../lib/connectors'

import BNBHiveContext from './BNBHiveContext'
import { BNBHiveAccount, BNBHiveStats } from './interfaces'

const BNBHiveProvider: React.FC<any> = ({ children }) => {
  const { chainId, library, account: accountAddress } = useActiveWeb3React()
  //console.log('ChainId', chainId, accountAddress, library)
  const referrerAddress = useReferrer()
  const { fastRefresh } = useRefresh()

  const contractAddress = useMemo(
    () => config.depositContract[chainId as ChainId],
    [chainId],
  )

  const bnbHiveContract = useMemo(() => {
    if (!chainId) return null
    let provider = new Web3Provider(RPC[chainId as ChainId], chainId as ChainId)
    if (library) {
      provider = new Web3Provider(library, chainId as ChainId)
    }
    const contract = new BnbHiveContract(contractAddress, provider)
    return contract
  }, [chainId, contractAddress, library])

  const [account, setAccount] = useState<BNBHiveAccount>({
    honey: 0,
    rewards: 0,
    dailyIncome: 0,
    bees: 0,
    minDeposit: 0,
    totalDeposit: 0,
    maxDeposit: 0,
    referralsCount: 0,
    referralsIncome: 0,
    referrals: {},
    loading: true,
  })
  const [stats, setStats] = useState<BNBHiveStats>({
    initialized: false,
    devFee: 0,
    vaultFee: 0,
    dailyReturnPercent: 0,
    owner: '',
    refLevels: [],
    loading: true,
  })
  useEffect(() => {
    const fetchStats = async () => {
      try {
        if (bnbHiveContract) {
          setStats(await bnbHiveContract.getStats())
        } else {
          setStats((prevState: BNBHiveStats) => ({
            ...prevState,
            loading: false,
          }))
        }
      } catch (e) {
        console.error(e)
      }
    }
    fetchStats()
  }, [bnbHiveContract, fastRefresh])
  useEffect(() => {
    const fetchAccount = async () => {
      try {
        if (accountAddress && bnbHiveContract) {
          setAccount(await bnbHiveContract.getAccountInfo(accountAddress))
        } else {
          setAccount((prevState: BNBHiveAccount) => ({
            ...prevState,
            loading: false,
          }))
        }
      } catch (e) {
        console.error(e)
      }
    }

    fetchAccount()
  }, [bnbHiveContract, accountAddress, fastRefresh])
  const value = {
    account,
    stats,
    contract: bnbHiveContract,
    referrerAddress,
  }
  return (
    <BNBHiveContext.Provider value={value}>{children}</BNBHiveContext.Provider>
  )
}

export default BNBHiveProvider