
import { useState, useEffect } from 'react'
import { useWeb3React } from '@web3-react/core'

import {
  injected,
  walletConnectWithDefaultChainId,
  WALLET_CONNECTOR,
  walletLocalStorageKey,
  walletConnectLocalStorageKey,
} from '../lib/connectors'

import { isMobile } from 'react-device-detect'
import { useWeb3React as useWeb3ReactCore } from '@web3-react/core'

export function useEagerConnect() {
  const { activate, active } = useWeb3ReactCore() // specifically using useWeb3ReactCore because of what this hook does
  const [tried, setTried] = useState(false)

  useEffect(() => {
    const lSConnector = localStorage.getItem(walletLocalStorageKey)

    if (lSConnector === WALLET_CONNECTOR.METAMASK) {
      injected.isAuthorized().then((isAuthorized) => {
        if (isAuthorized) {
          activate(injected, undefined, true).catch(() => {
            setTried(true)
          })
        } else if (isMobile && (window as any).ethereum) {
          activate(injected, undefined, true).catch(() => {
            setTried(true)
          })
        } else {
          setTried(true)
        }
      })
    } else if (lSConnector === WALLET_CONNECTOR.WALLET_CONNECT) {
      const currentConnector = localStorage.getItem(
        walletConnectLocalStorageKey,
      )
      if (!currentConnector) {
        localStorage.removeItem(walletLocalStorageKey)
        setTried(true)
      } else {
        const connectorChainId = (JSON.parse(currentConnector) || {}).chainId
        const walletConnect = walletConnectWithDefaultChainId(connectorChainId)
        activate(walletConnect, undefined, true).then(() => {
          setTried(true)
        })
      }
    } else if (lSConnector === WALLET_CONNECTOR.TRUST_WALLET) {
      activate(injected, undefined, true).catch(() => {
        setTried(true)
      })
    } else {
      setTried(true)
    }
  }, [activate]) // intentionally only running on mount (make sure it's only mounted once :))

  // if the connection worked, wait until we get confirmation of that to flip the flag
  useEffect(() => {
    if (active) {
      setTried(true)
    }
  }, [active])

  return tried
}

export function useInactiveListener(suppress = false) {
  const { active, error, activate } = useWeb3React()

  useEffect(() => {
    const { ethereum } = window
    if (ethereum && ethereum.on && !active && !error && !suppress) {
      const handleChainChanged = (chainId: string | number) => {
        if (process && process.env.NODE_ENV === 'development') {
          console.log('chainChanged', chainId)
        }
        activate(injected)
      }

      const handleAccountsChanged = (accounts: string[]) => {
        if (process && process.env.NODE_ENV === 'development') {
          console.log('accountsChanged', accounts)
        }
        if (accounts.length > 0) {
          activate(injected)
        }
      }

      const handleNetworkChanged = (networkId: string | number) => {
        if (process && process.env.NODE_ENV === 'development') {
          console.log('networkChanged', networkId)
        }
        activate(injected)
      }

      ethereum.on('chainChanged', handleChainChanged)
      ethereum.on('accountsChanged', handleAccountsChanged)
      ethereum.on('networkChanged', handleNetworkChanged)

      return () => {
        if (ethereum.removeListener) {
          ethereum.removeListener('chainChanged', handleChainChanged)
          ethereum.removeListener('accountsChanged', handleAccountsChanged)
          ethereum.removeListener('networkChanged', handleNetworkChanged)
        }
      }
    }

    return () => {}
  }, [active, error, suppress, activate])
}