
import React from 'react'
import { BrowserRouter as Router } from 'react-router-dom'
import { Web3ReactProvider } from '@web3-react/core'
import {
  ExternalProvider,
  JsonRpcFetchFunc,
  Web3Provider,
} from '@ethersproject/providers'
import { Container, Row, Col } from 'react-bootstrap'
import { ToastContainer } from 'react-toastify'

import Header from './components/Header/Header'

import './App.css'
import ReferralSystemSection from './sections/ReferralSystemSection/ReferralSystemSection'
import ReHireSection from './sections/ReHireSection/ReHireSection'
import InfoSection from './sections/InfoSection/InfoSection'
import HireSection from './sections/HireSection/HireSection'
import FAQSection from './sections/FAQSection/FAQSection'
import Footer from './components/Footer/Footer'
import Web3ProviderNetwork from './providers/Web3ProviderNetwork'
import BNBHiveProvider from './providers/BNBHiveProvider/BNBHiveProvider'
import RefreshContextProvider from './providers/RefreshProvider'
import Web3ReactManager from './components/Web3ReactManager'

import { useEffect, useState } from "react";
import config from "./config"
window.hive = config

function getLibrary(provider: ExternalProvider | JsonRpcFetchFunc): any {
  return provider
  // const library = new Web3Provider(provider);
  // library.pollingInterval = 8000;
  // return library;
}

function App() {
  //const [title, setTitle] = useState("Hive Miner");

  useEffect(() => {
    // This will run when the page first loads and whenever the title changes
    document.title = `${config.symbol} Hive Miner V2 Multichain`;
  }, []);


  return (
    <Router>
      <Web3ReactProvider getLibrary={getLibrary}>
        <Web3ProviderNetwork getLibrary={getLibrary}>
          <Web3ReactManager>
            <RefreshContextProvider>
              <BNBHiveProvider>
                <div className="app" >
                  <Header />
                  <div>
                    <Container>
                      <Row>
                        <Col xs={12} md={6} lg={6}>
                          <HireSection />
                        </Col>
                        <Col xs={12} md={6} lg={6}>
                          <ReHireSection />
                        </Col>
                      </Row>
                    </Container>
                    <Container>
                      <Row>
                        <Col xs={12}>
                          <InfoSection />
                        </Col>
                      </Row>
                    </Container>
                    <ReferralSystemSection />
                    <Container>
                      <FAQSection />
                    </Container>
                  </div>
                  <Footer />
                  <ToastContainer />
                </div>
              </BNBHiveProvider>
            </RefreshContextProvider>
          </Web3ReactManager>
        </Web3ProviderNetwork>
      </Web3ReactProvider>
    </Router>
  )
}

export default App