import { useEffect, useState } from 'react'
import { useWeb3React as useWeb3ReactCore } from '@web3-react/core'
import { Web3ReactContextInterface } from '@web3-react/core/dist/types'

import { createNetwork } from '../lib/connectors'
import { NetworkContextName } from '../constants'
import ChainId from '../constants/ChainId'
import Web3Provider from '../contracts/utils/Web3Provider'

export const useActiveWeb3React = (): Web3ReactContextInterface<any> & {
  chainId?: ChainId
  balance: string
  setChainId: (chainId: ChainId) => void
} => {
  const [balance, setBalance] = useState<string>('0')
  // replace with address to impersonate
  const impersonate = false
  const contextUser = useWeb3ReactCore()
  const contextNetwork = useWeb3ReactCore(NetworkContextName)
  const context = contextUser.active ? contextUser : contextNetwork

  context.account = impersonate || context.account

  const setChainId = (chainId: ChainId) => {
    if (!contextUser.active) {
      const network = createNetwork(chainId)
      context.activate(network)
    }
  }

  useEffect(() => {
    const fetchBalance = async () => {
      if (!context.account) {
        setBalance('0')
        return
      }
      if (context.library) {
        const provider = new Web3Provider(
          context.library as any,
          context.chainId as ChainId,
        )
        const balance = await provider.web3.eth.getBalance(context.account)
        if (balance) {
          setBalance(balance.toString())
        }
      }
    }

    fetchBalance()
  }, [context.account, context.library, context.chainId])

  return {
    ...context,
    balance,
    setChainId,
  }
}

export default useActiveWeb3React