
import { createContext, useContext } from "react";
import BnbHiveContract from "../../contracts/BnbHive";

import { BNBHiveAccount, BNBHiveStats } from "./interfaces";

export interface IBNBHiveContext {
  account: BNBHiveAccount;
  contract?: BnbHiveContract | null;
  referrerAddress?: string;
  stats: BNBHiveStats;
}

const BNBHiveContext = createContext<IBNBHiveContext>({
  account: {
    honey: 0,
    rewards: 0,
    dailyIncome: 0,
    bees: 0,
    totalDeposit: 0,
    minDeposit: 0,
    maxDeposit: 0,
    referralsCount: 0,
    referralsIncome: 0,
    referrals: {},
    loading: true,
  },
  stats: {
    loading: true,
    vaultFee: 0,
    devFee: 0,
    dailyReturnPercent: 0,
    initialized: false,
    tvl: "0",
    owner: "",
    refLevels: [],
  },
});

export default BNBHiveContext;

export const useBnbHive = (): IBNBHiveContext => {
  return useContext<IBNBHiveContext>(BNBHiveContext);
};