import React, { useState } from 'react'
import { CopyToClipboard } from 'react-copy-to-clipboard'
import { useWeb3React } from '@web3-react/core'
import { Row, Col, Container } from 'react-bootstrap'

import Card from '../../components/Card/Card'
import Section from '../../components/Section/Section'
import { useBnbHive } from '../../providers/BNBHiveProvider/BNBHiveContext'

import './ReferralSystemSection.css'
import { FaCopy } from 'react-icons/fa'
import ReferralLevels from '../../components/ReferralLevels/ReferralLevels'
import InfoItem from '../../components/InfoItem/InfoItem'
import Loader from '../../components/Loader/Loader'
import config from '../../config'
const {symbol} = config

const ReferralSystemSection = () => {
  const { account: address } = useWeb3React()
  const { account, stats } = useBnbHive()
  const [copied, setCopied] = useState<boolean>(false)
  const link = `${window.location.origin}?ref=${address || '...'}`
  const onCopy = () => {
    setCopied(true)
    setTimeout(() => {
      setCopied(false)
    }, 3000)
  }
  const showRefLink = account.totalDeposit > 0 || address === stats.owner
  return (
    <Section className="referral-system-section">
      <Container>
        <Row>
          <Col xs={12} lg={6}>
            <Card title="Referral System">
              {showRefLink ? (
                <>
                  <div className="referral-system__link-wrapper">
                    <div className="referral-system__link">{link}</div>
                    {copied ? (
                      <div>Copied</div>
                    ) : (
                      <CopyToClipboard text={link} onCopy={onCopy}>
                        <FaCopy
                          className="referral-system__link-copy"
                          size={26}
                        />
                      </CopyToClipboard>
                    )}
                  </div>
                </>
              ) : (
                <>
                  <div className="referral-system__link-wrapper">
                    Deposit to get a referral link.
                  </div>
                </>
              )}

              <p>
                Earn up to 10% of the {symbol} from anyone who uses your referral
                link to open the farm
              </p>
              {stats.loading ? (
                <Loader />
              ) : (
                stats.refLevels.map((item, index) => {
                  return (
                    <InfoItem key={index} label={`Level ${index + 1}:`}>
                      {item.percent}%
                    </InfoItem>
                  )
                })
              )}
            </Card>
          </Col>
          <Col xs={12} lg={6}>
            <Card title="Referral statistics">
              <ReferralLevels />
            </Card>
          </Col>
        </Row>
      </Container>
    </Section>
  )
}

export default ReferralSystemSection
