import React, { useEffect, useState } from 'react'
import NavDropdown from 'react-bootstrap/NavDropdown';
import "./NetworkSelector.css"
import { network } from '../../lib/connectors'
import chains from './chains';
import config from '../../config'

function NetworkSelector() {
    const [currentChain, setCurrentChain] = useState(chains[config.chainId.mainnet])
    useEffect(() => {
        async function getChainId() {
            let chainId = await network.getChainId()
            setCurrentChain(chains[chainId])
        }
        getChainId()
    })
    //console.log(currentChain);
    return (
        <NavDropdown
            id="nav-dropdown-dark-example"
            title={currentChain.name}
            menuVariant="dark"
            className="network-selector"
        >
            {
                config.chainsAvailable.map((c: any, i: number) => {
                    return (<NavDropdown.Item key={i} href={c.url}>{c.name}</NavDropdown.Item>)
                })
            }

        </NavDropdown>
    );
}

export default NetworkSelector;