
import React from 'react';
import cn from 'classnames';

import './Input.css';

const Input = ({className, affix, ...props}: any) => {
    const inputClassNames = cn("input", className, {
        "input--with-affix": affix
    });
    return (
        <div className="input-wrapper">
            <input className={inputClassNames} {...props} />
            {affix && (
                <div className="input-wrapper__affix">
                    {affix}
                </div>
            )}
        </div>
    )
}

export default Input;