import BigNumber from 'bignumber.js'

export const shortenAddress = (address: string, chars = 4): string => {
  return `${address.substring(0, chars + 2)}...${address.substring(42 - chars)}`
}

export const roundTo = (value?: string | number, digits: number = 2): number => {
  if (!value) return 0
  const localValue = typeof value === 'string' ? parseFloat(value) : value
  const multiplier = 10 ** digits
  return Math.round(localValue * multiplier) / multiplier
}

export const toWeiWithDecimals = (value: string, decimals: number = 18) => {
  const formattedValue = new BigNumber(value)
  const divisor = new BigNumber(10).pow(new BigNumber(decimals))
  return formattedValue.multipliedBy(divisor).integerValue().toFixed()
}

export const fromWeiWithDecimals = (value: string, decimals: number = 18) => {
  const formattedValue = new BigNumber(value)
  const divisor = new BigNumber(10).pow(new BigNumber(decimals))
  return formattedValue.dividedBy(divisor).toString()
}

export const toNumberFromWei = (value: string, decimals: number = 18) => {
  return parseFloat(fromWeiWithDecimals(value, decimals))
}

export const toBigN = (a: string) => new BigNumber(a)

export async function sleep(ms?: number): Promise<any> {
  return new Promise((resolve) => {
    setTimeout(() => resolve(undefined), ms || 1000)
  })
}