import { EventData, PastEventOptions } from 'web3-eth-contract'
import { AbiItem } from 'web3-utils'

import Web3Provider from './utils/Web3Provider/'

import { sleep } from '../utils/index'

class EthContract {
  address: string

  web3Provider: Web3Provider

  contract: any

  constructor(
    address: string,
    abi: AbiItem | AbiItem[],
    web3Provider: Web3Provider,
  ) {
    this.address = address
    this.web3Provider = web3Provider
    this.contract = new web3Provider.web3.eth.Contract(abi)
    this.contract.options.address = address
  }

  async getPastEvents(
    event: string,
    options: PastEventOptions,
  ): Promise<EventData[]> {
    const res = await this.contract.getPastEvents(event, options)

    return res
  }

  getVariable = async (name: string, ...args: any[]): Promise<any> => {
    try {
      const fn = this.contract.methods[name](...args)
      const res = await fn.call()

      return res
    } catch (e) {
      // eslint-disable-next-line no-console
      console.error('Error', e)
      console.log(this.contract, this.contract.methods, name, ...args);
      return null
    }
  }

  async getEvents(
    eventName: string,
    fromBlock: string = '0',
    toBlock: string = '0',
    filter?: any,
  ): Promise<EventData[]> {
    const step = 5000

    let currentBlock = parseInt(fromBlock)

    let events: any[] = []

    const lastBlockNumber: number =
      parseInt(toBlock) || (await this.web3Provider.web3.eth.getBlockNumber())

    console.log('lastBlockNumber', lastBlockNumber)

    const diff = lastBlockNumber - currentBlock
    if (diff < 0) return []

    let retry = 0
    while (currentBlock <= lastBlockNumber) {
      try {
        console.log('currentBlock', currentBlock, events.length)

        if (retry > 2) {
          console.error(
            `BLOCKS ${currentBlock} - ${currentBlock + step} SKIPPED`,
          )
        } else {
          // eslint-disable-next-line no-await-in-loop
          const newEvents = await this.getPastEvents(eventName, {
            filter,
            fromBlock: currentBlock,
            toBlock: Math.min(currentBlock + step, lastBlockNumber),
          })
          events = events.concat(newEvents)
        }

        currentBlock = currentBlock + step + 1
        retry = 0
      } catch (e) {
        // eslint-disable-next-line no-plusplus
        retry++
        // eslint-disable-next-line no-await-in-loop
        await sleep(1000)
      }
    }

    return events
  }

  async getEventsByInterval(
    eventName: string,
    fromBlock: string,
    toBlock: string,
    filter?: any,
  ): Promise<EventData[]> {
    let retry = 0
    let events: any[] = []
    while (retry <= 2) {
      try {
        // eslint-disable-next-line no-await-in-loop
        events = await this.getPastEvents(eventName, {
          filter,
          fromBlock,
          toBlock,
        })
        break
      } catch (e) {
        // eslint-disable-next-line no-plusplus
        retry++
        console.log('retry', retry, e)
        if (retry <= 2) {
          // eslint-disable-next-line no-await-in-loop
          await sleep(1000 * retry)
        }
      }
    }

    if (retry > 2) {
      throw new Error('BLOCKS FETCH ERROR')
    }

    return events
  }

  // eslint-disable-next-line consistent-return
  async getEventsByIntervals(
    eventName: string,
    fromBlock: string,
    toBlock: string,
    filter?: any,
  ) {
    const step = 5000
    let currentBlock = parseInt(fromBlock)

    let events: any[] = []

    const lastBlockNumber: number =
      parseInt(toBlock) || (await this.web3Provider.web3.eth.getBlockNumber())

    console.log('lastBlockNumber', lastBlockNumber)

    const diff = lastBlockNumber - currentBlock

    if (diff < 0) return []

    while (currentBlock <= lastBlockNumber) {
      const toBlock = Math.min(currentBlock + step, lastBlockNumber)
      // eslint-disable-next-line no-await-in-loop
      const newEvents = await this.getEventsByInterval(
        eventName,
        currentBlock.toString(),
        toBlock.toString(),
        filter,
      )
      events = events.concat(newEvents)
      currentBlock = currentBlock + step + 1
    }
  }
}

export default EthContract