
import React, { useState } from 'react'

import Card from '../../components/Card/Card'
//import HelpfulLinks from '../../components/HelpfulLinks'

import Section from '../../components/Section/Section'

import './FAQSection.css'
import config from '../../config'
const {symbol, reinvestTarget, firstWeekDepositCap} = config

const faq = [
  {
    question: 'WHAT HAPPENS WHEN I HIRE BEES?',
    answer:
      `When you hire bees, you send ${symbol} to the hive smart contract and receive a fixed daily reward of 3% of the amount. You can always reinvest your rewards to hire more bees and get more ${symbol} rewards.`,
  },
  {
    question: 'HOW MUCH ARE MY FEES?',
    answer:
      `There is a total fee of 5%, It consists of Dev fee - 3% and vault fee -2%. Dev fee is needed in order for the ${symbol} Hive ecosystem to be developed.`,
  },
  {
    question: 'WHAT IS A VAULT?',
    answer:
      `A Vault is a special storage that supports the main smart contract of the hive. Each beekeeper fills out the save by 2%. This is a fund that automatically replenishes the balance of the contract if necessary. We  invest it in favor of the project instead of taking this award for ourselves.`,
  },
  {
    question: 'WHAT IS THE MAXIMUM DEPOSIT AMOUNT?',
    answer:
      `In order for everything to be fair, we have limited the deposit that can be made from one account in the 1st week to ${firstWeekDepositCap} ${symbol} and we will increase the possible deposit amount by 100% every week, so after the first week it will be possible to invest ${firstWeekDepositCap*2} ${symbol} and so on.`,
  },
  {
    question: 'HOW DO REFERRALS WORK?',
    answer:
      `There are 5 lines in the ${symbol} hive referral program with the following percentages: 10%, 7%, 5%, 3% and 1%. When you connect your wallet to the site, you will receive your referral link in a special line. When a new user buys bees after clicking your personal referral link, the contract will send ${symbol} DIRECTLY to your wallet. When a user reinvests in bees, you also get a referral bonus. All missed profit income is automatically distributed to the hive support Vault!`,
  },
  {
    question: 'HOW OFTEN SHOULD I REINVEST?',
    answer:
      `We recommend you to reinvest once per day, but you can do it whenever you want! Minimum amount to reinvest is ${reinvestTarget} ${symbol}.`,
  },
  {
    question: `WHEN DID ${symbol} HIVE LAUNCH?`,
    answer: `We launched on 5th of June, 2022 18:00 UTC`,
  },
]

const FAQItem: React.FC<any> = ({
  question,
  children,
  defaultCollapsed = true,
}) => {
  const [collapsed, setCollapsed] = useState<boolean>(defaultCollapsed)
  const toggleCollapse = () => setCollapsed((prevState: boolean) => !prevState)
  return (
    <div className="faq-item">
      <div className="fag-item__header" onClick={toggleCollapse}>
        <div className="faq-item__question">{question}</div>
        <div className="faq-item__collapser">{collapsed ? '+' : '-'}</div>
      </div>
      {collapsed ? null : (
        <div className="faq-item__body">
          <div className="faq-item__answer">{children}</div>
        </div>
      )}
    </div>
  )
}

const FAQSection = () => {
  return (
    <Section className="faq-section">
      <Card title="FAQ">
        {faq.map((item, index) => (
          <FAQItem
            key={index}
            question={`${index + 1}. ${item.question}`}
            defaultCollapsed={index >= 1}
          >
            {item.answer}
          </FAQItem>
        ))}
      </Card>
      {/* <Card title="Helpful links">
        <HelpfulLinks />
      </Card> */}
    </Section>
  )
}

export default FAQSection