
import React, { useEffect, useState } from 'react'
import { UnsupportedChainIdError, useWeb3React } from '@web3-react/core'
import { Web3Provider } from '@ethersproject/providers'
import { BiLogOut } from 'react-icons/all'

import { useEagerConnect, useInactiveListener } from '../../hooks'
import connectorList, {
  walletConnectLocalStorageKey,
  walletConnectWithDefaultChainId,
  walletLocalStorageKey,
  WALLET_CONNECTOR,
} from '../../lib/connectors'
import config from '../../config'
import Button from '../Button/Button'

import './ConnectWallet.css'
import { shortenAddress } from '../../utils'
import ConnectWalletModal from './ConnectWalletModal'
import { WalletConnectConnector } from '@web3-react/walletconnect-connector'
import { AbstractConnector } from '@web3-react/abstract-connector'
import usePrevious from '../../hooks/usePrevious'

type PendingWallet = AbstractConnector | undefined

const ConnectWallet = () => {
  const [isOpen, setIsOpen] = useState<boolean>(false)
  const [pendingError, setPendingError] = useState<any>(null)
  const [isConnecting, setIsConnecting] = useState(false)
  const [pendingWallet, setPendingWallet] = useState<{
    connector: PendingWallet
    walletName?: string
  }>()
  const {
    chainId,
    activate,
    deactivate,
    connector,
    active,
    account,
    error,
  } = useWeb3React<Web3Provider>()

  const handleClick = (connectorName: WALLET_CONNECTOR) => () => {
    setIsConnecting(true)
    tryActivation(connectorList[connectorName], connectorName)
  }

  const handleDisconnect = () => {
    deactivate()
    localStorage.removeItem(walletLocalStorageKey)
    if (connector instanceof WalletConnectConnector) {
      ;(connector as WalletConnectConnector).close()
      ;(connector as WalletConnectConnector).walletConnectProvider = undefined
      localStorage.removeItem(walletConnectLocalStorageKey)
    }
  }

  const activePrevious = usePrevious(active)
  const connectorPrevious = usePrevious(connector)

  const tryActivation = async (
    connector: PendingWallet,
    walletName?: string,
  ) => {
    setPendingWallet({ connector, walletName }) // set wallet for pending view
    // setWalletView(WALLET_VIEWS.PENDING)

    // if the connector is walletconnect and the user has already tried to connect, manually reset the connector
    if (connector instanceof WalletConnectConnector) {
      const provider = connector.walletConnectProvider as any
      if (provider && provider.wc && provider.wc.uri) {
        // eslint-disable-next-line no-param-reassign
        connector.walletConnectProvider = undefined
      }
      // eslint-disable-next-line no-param-reassign
      connector = walletConnectWithDefaultChainId(chainId)
    }
    if (connector) {
      activate(connector, undefined, true)
        .then(() => {
          localStorage.setItem(walletLocalStorageKey, walletName || '')
        })
        .catch((error) => {
          if (error instanceof UnsupportedChainIdError) {
            activate(connector as AbstractConnector)
          } else {
            setPendingError(true)
          }
        })
        .finally(closeModal)
    }
  }

  useEffect(() => {
    if (active) {
      setIsConnecting(false)
    }
  }, [active])

  const openModal = () => setIsOpen(true)
  const closeModal = () => {
    setIsConnecting(false)
    setIsOpen(false)
  }

  return (
    <div className="connect-wallet">
      {active ? (
        <>
          <div className="connect-wallet__account">
            {shortenAddress(account || '')}
          </div>
          <Button
            className="connect-wallet__disconnect-button"
            onClick={handleDisconnect}
          >
            <BiLogOut />
          </Button>
        </>
      ) : (
        <Button type="secondary" onClick={openModal} style={{"background-color": config.bgColor}}>
          Connect Wallet
        </Button>
      )}
      <ConnectWalletModal
        isOpen={isOpen}
        onSelectConnector={handleClick}
        isConnecting={isConnecting}
        onClose={closeModal}
      />
    </div>
  )
}

export default ConnectWallet