import React from 'react'
import cn from 'classnames'

import Loader from '../Loader/Loader'

import './InfoItem.css'

interface InfoItem {
  label: string
  children: any
  spaceBetween?: boolean
  loading?: boolean
}

const InfoItem: React.FC<InfoItem> = ({
  label,
  children,
  spaceBetween = true,
  loading,
}) => {
  const className = cn('info-item', {
    'info-item--space-between': spaceBetween,
  })
  return (
    <div className={className}>
      <div className="info-item__label">{label}</div>
      <div className="info-item__value">
        {loading ? <Loader size="sm" /> : children}
      </div>
    </div>
  )
}

export default InfoItem