
import React, { useState } from 'react'

import Button from '../../components/Button/Button'
import Card from '../../components/Card/Card'
import ConnectWallet from '../../components/ConnectWallet/ConnectWallet'
import Section from '../../components/Section/Section'
import Socials from '../../components/Socials/Socials'
import useActiveWeb3React from '../../hooks/useActiveWeb3React'
import { useBnbHive } from '../../providers/BNBHiveProvider/BNBHiveContext'
import { fromWeiWithDecimals, roundTo } from '../../utils'

import './ReHireSection.css'
import config from '../../config'
const {symbol, reinvestTarget, claimTarget} = config


const ReHireSection = () => {
  const { account: accountAddress } = useActiveWeb3React()
  const { account, contract, referrerAddress } = useBnbHive()
  const [loading, setLoading] = useState<boolean>(false)
  const rewards = roundTo(account.rewards || '0', 4)
  const honey = roundTo(account.honey, 3);
  const canClaim = rewards > claimTarget;
  const cnaReinvest = rewards > reinvestTarget;

  const onReHire = async () => {
    if (accountAddress && contract) {
      setLoading(true)
      try {
        const bnbReward = fromWeiWithDecimals(await contract.getRewards(accountAddress));
        const bnbAmountReinvest = parseFloat(bnbReward) * 0.95;
        await contract.withdraw(accountAddress);
        await contract.deposit(bnbAmountReinvest.toString(), referrerAddress, accountAddress)
        setLoading(false)
      } catch (e) {
        alert((e as any).message)
        setLoading(false)
      }
    }
  }

  const onClaim = async () => {
    if (accountAddress && contract) {
      setLoading(true)
      try {
        await contract.withdraw(accountAddress)
        setLoading(false)
      } catch (e) {
        alert((e as any).message)
        setLoading(false)
      }
    }
  }

  return (
    <Section className="re-hire-section" small>
      <Card title="Your honey">
        <div className="premium-amount">
          {honey} HONEY / {rewards} {symbol}
        </div>
        <div className="re-hire__buttons">
          {!accountAddress ? (
            <ConnectWallet />
          ) : (
            <>
              <Button
                disabled={!cnaReinvest}
                type="primary"
                onClick={onReHire}
                loading={loading}
              >
                Hire more bees
              </Button>
              <Button
                disabled={!canClaim}
                type="secondary"
                onClick={onClaim}
                loading={loading}
              >
                Claim {symbol}
              </Button>
            </>
          )}
        </div>
        <div className="re-hire__note">
          <p>* To claim you need to collect more then {claimTarget} {symbol}. To rehire you need to collect more then {reinvestTarget} {symbol}.</p>
          <p>On rehire you will need to confirm 2 txs.</p>
        </div>
        <div>
          <div className="card-title">Socials</div>
          <Socials />
        </div>
      </Card>
    </Section>
  )
}

export default ReHireSection