import { useState, useEffect } from 'react'
import { useSearchParams } from 'react-router-dom'
import { isAddress } from 'web3-utils'
import { ZERO_ADDRESS } from '../constants'

const LC_REFERRER_KEY = 'LC_REFERRER_KEY'

const useReferrer = () => {
  const [searchParams] = useSearchParams()
  const queryReferrerAddress = searchParams.get('ref')
  const [referrerAddress, setReferrerAddress] = useState<string>(ZERO_ADDRESS)

  useEffect(() => {
    const lcValue = localStorage.getItem(LC_REFERRER_KEY)
    if (lcValue && isAddress(lcValue)) {
      setReferrerAddress(lcValue)
    }
  }, [])

  useEffect(() => {
    if (queryReferrerAddress && isAddress(queryReferrerAddress)) {
      if (referrerAddress === ZERO_ADDRESS) {
        setReferrerAddress(queryReferrerAddress)
        localStorage.setItem(LC_REFERRER_KEY, queryReferrerAddress)
      }
    }
  }, [queryReferrerAddress])

  return referrerAddress
}

export default useReferrer