import React from 'react'
import { Modal } from 'react-bootstrap'
import { WALLET_CONNECTOR } from '../../lib/connectors'
import Loader from '../Loader/Loader'
import config from '../../config'
const connectors = [
  {
    key: WALLET_CONNECTOR.METAMASK,
    title: 'MetaMask',
    icon: '/images/wallets/metamask.svg',
  },
  {
    key: WALLET_CONNECTOR.WALLET_CONNECT,
    title: 'WalletConnect',
    icon: '/images/wallets/wallet-connect.svg',
  },
]

interface ConnectWalletModalProps {
  isOpen?: boolean
  isConnecting?: boolean
  onSelectConnector: (connector: WALLET_CONNECTOR) => (e: any) => void
  onClose: () => void
}

const ConnectWalletModal: React.FC<ConnectWalletModalProps> = ({
  isOpen,
  isConnecting,
  onSelectConnector,
  onClose,
}) => {
  let style = {"backgroundColor": config.bgColor}
  return (
    <Modal className="connect-wallet__modal fantom-bg" show={isOpen} onHide={onClose} >
      <Modal.Title>Connect your wallet</Modal.Title>
      {isConnecting ? (
        <div>
          <Loader />
        </div>
      ) : (
        <div className="connect-wallet__options" >
          {connectors.map((option, index) => {
            return (
              <div
                key={option.key}
                className="connect-wallet__option"
                onClick={onSelectConnector(option.key)}
              >
                <div className="connect-wallet__option-icon">
                  <img src={option.icon} alt={option.title} />
                </div>
                <div className="connect-wallet__option-title">
                  {option.title}
                </div>
              </div>
            )
          })}
        </div>
      )}
    </Modal>
  )
}

export default ConnectWalletModal