import { AbiItem } from "web3-utils";
import { ZERO_ADDRESS } from "../../constants";
import { BNBHiveAccount } from "../../providers/BNBHiveProvider/interfaces";
import {
  fromWeiWithDecimals,
  toNumberFromWei,
  toWeiWithDecimals,
} from "../../utils";
import BnbVaultContract from "../BnbHiveVault";
import EthContract from "../EthContract";

import Web3Provider from "../utils/Web3Provider";

import ABI from "./abi/bnb-hive.json";

class BnbHiveContract extends EthContract {
  constructor(address: string, web3Provider: Web3Provider) {
    super(address, ABI as AbiItem[], web3Provider);
  }

  getContractBalance = async () => {
    return await this.getVariable("getBalance");
  };

  getVaultBalance = async () => {
    const vaultAddress = await this.getVariable("vaultAddress");
    const vaultContract = new BnbVaultContract(vaultAddress, this.web3Provider);

    return vaultContract.getContractBalance();
  };

  getHoney = async (address: string) => {
    return await this.getVariable("getHoney", address);
  };

  getMiners = async (address: string) => {
    return await this.getVariable("getBees", address);
  };

  getRewards = async (address: string) => {
    return await this.getVariable("bnbRewards", address);
  };

  getDailyIncome = async (address: string) => {
    return await this.getVariable("calculateDailyIncome", address);
  };

  getReferralsCount = async (address: string, level: number) => {
    const referralsCount = await this.getVariable(
      "getReferralsCount",
      address,
      level
    );
    return parseInt(referralsCount);
  };

  getReferralsIncome = async (address: string, level: number) => {
    const referralsIncome = await this.getVariable(
      "getReferralsIncome",
      address,
      level
    );

    return toNumberFromWei(referralsIncome);
  };

  getReferrals = async (address: string) => {
    const referrals: any = {};
    for (let i = 1; i < 6; i++) {
      referrals[i] = {
        count: await this.getReferralsCount(address, i),
        income: await this.getReferralsIncome(address, i),
      };
    }

    return referrals;
  };

  getIsInitialized = async () => await this.getVariable("initialized");

  getAccountInfo = async (address: string) => {
    const user = await this.getVariable("users", address);
    const maxDeposit = await this.getVariable("getMaxDeposit", address);
    const minDeposit = await this.getVariable("MIN_DEPOSIT");
    return {
      rewards: toNumberFromWei(await this.getRewards(address)),
      dailyIncome: toNumberFromWei(await this.getDailyIncome(address)),
      honey: toNumberFromWei(await this.getHoney(address)),
      minDeposit: toNumberFromWei(minDeposit),
      bees: toNumberFromWei(user.hiredBees, 9),
      totalDeposit: toNumberFromWei(user.totalDeposit),
      maxDeposit: toNumberFromWei(maxDeposit),
      referralsCount: parseFloat(user.totalRefs),
      referralsIncome: toNumberFromWei(user.totalRefIncome),
      referrals: await this.getReferrals(address),
      loading: false,
    } as BNBHiveAccount;
  };

  getReferralLevels = async () => {
    const levels = [];

    for (let i = 1; i <= 5; i++) {
      const refLevelPercent = await this.getVariable("getRefLevelPercent", i);
      /* const refLevelDepositAmount = await this.getVariable(
        "MIN_DEPOSIT_TO_ENABLE_REF_LEVEL",
        i
      ); */
      levels.push({
        level: i,
        percent: refLevelPercent,
        minDeposit: 0.1/* toNumberFromWei(refLevelDepositAmount) */,
      });
    }

    return levels;
  };

  getStats = async () => {
    const devFee = await this.getVariable("DEV_FEE_PERCENT");
    const vaultFee = await this.getVariable("VAULT_FEE_PERCENT");
    const hiveToHatch1miner = await this.getVariable("HONEY_TO_CLAIM_ONE_BEE");

    return {
      initialized: await this.getIsInitialized(),
      tvl: await this.getContractBalance(),
      vault: await this.getVaultBalance(),
      devFee: devFee,
      vaultFee: vaultFee,
      // dailyReturnPercent: ((60 * 60 * 24) / hiveToHatch1miner) * 100,
      dailyReturnPercent: 2.1,
      owner: await this.getVariable("owner"),
      refLevels: await this.getReferralLevels(),
      loading: false,
    };
  };

  deposit = async (
    amount: string,
    referrerAddress: string = ZERO_ADDRESS,
    from: string
  ) => {
    if (referrerAddress === ZERO_ADDRESS) {
      referrerAddress = await this.contract.methods.top().call();
    }

    return await this.contract.methods
      .deposit(referrerAddress)
      .send({ value: toWeiWithDecimals(amount), from });
  };

  reinvest = async (from: string) => {
    return await this.contract.methods.reinvest().send({ from });
  };

  withdraw = async (from: string) => {
    return await this.contract.methods.withdraw().send({ from });
  };
}

export default BnbHiveContract;