import React from 'react'
import cn from 'classnames'
import config from '../../config'
import './Card.css'

const Card: React.FC<any> = ({ children, className, title }) => (
  <div className={cn('hive-card', className)} style={{background: config.bgColor}}>
    <div className="hive-card__content">
      {title && <div className="hive-card__title">{title}</div>}
      <div className="hive-card__body">{children}</div>
    </div>
  </div>
)

export default Card